<template>
    <div class="div1">
        <div style="padding:10px;">
            <h3 style="text-align:center; color:royalblue; font-size:50px;">JOIN</h3>
            <div style="text-align:center">
                <label></label><br>
                <input type="text" v-model="state.id" class="in" :ref="el => {form[0] = el}" 
                    @keyup="handleIDCheck">
            </div>
            <div style="text-align:center">
                <div v-html="state.idcheck" style="position: relative; bottom:44px; left:220px; font-size:small; height:0;"></div>
            </div>
            <div style="text-align:center;">
                <label></label><br>
                <input type="password" v-model="state.pw" class="in" :ref="el => {form[1] = el}">
            </div>
            <div style="text-align:center">
                <label> </label><br>
                <input type="password" v-model="state.pw2" class="in" :ref="el => {form[2] = el}">
            </div>
            <div style="text-align:center">
                <label></label><br>
                <input type="text" class="in" v-model="state.nickname"
                    :ref="el => {form[3] = el}" @keyup="handleNickcheck">
            </div>
            <div style="text-align:center">
                <div v-html="state.nickcheck" style="position: relative; bottom:44px; left:220px; font-size:small; height:0;"></div>
            </div>
            <div style="text-align:center">
                <label></label><br>
                <input type="text" placeholder = "ex) 010-1234-5678" v-model="state.phone" 
                    class="in" :ref="el => {form[4] = el}" @keyup="handlePhonecheck">
            </div>
            <div style="text-align:center">
                <div v-html="state.phonecheck" style="position: relative; bottom:44px; left:220px; font-size:small; height:0;"></div>
            </div>
            <div style="text-align:center">
                <label></label><br>
                <input type="number" class="in" v-model="state.birth" :ref="el => {form[5] = el}" placeholder="ex) 1994" />
            </div>
            <div style="text-align:center">
                <label> </label><br />
            </div>
            <div style="text-align:center;">
                <div style="display:inline-block;width:510px;padding:5px;border:1px solid lightgray;border-radius: 3px; background-color: white;overflow-y: scroll; height:140px;">
                    <p style="text-align:center; color: black;"> 
                        <span style="font-weight:bold">
                            <span style="color:orange">OTT</span>[<span style="color:royalblue"> i </span>]
                        </span>
                           .
                    </p>
                    <p style="text-align:left; color: black;">1. <span style="font-size:18px;font-weight:bold"></span>    .          .</p>
                    <p style="text-align:left; color: black;">2. <span style="font-size:18px;font-weight:bold"></span>         .</p>
                    <p style="text-align:left; color: black;">3. <span style="font-size:18px;font-weight:bold"></span>      . ,         .</p>
                    <p style="text-align:left; color: black;">4. <span style="font-size:18px;font-weight:bold"></span>    1:1   .</p>
                    <p style="text-align:left; color: black;">5. <span style="font-size:18px;font-weight:bold"></span>               .</p>
                </div><br />
                <div style="text-align:left; width:510px;display:inline-block;">
                        <input style="margin-top:20px;" type="checkbox" v-model="state.check" :ref="el => {form[6] = el}" />
                        <span style="margin-top:20px;"> OTT[i]   .</span>
                </div> 
            </div>
            <div style="text-align:center">
                <button @click="submit()" style="margin-top:15px; border:none;background-color: royalblue; border-radius: 3px;height:45px; width:513px; color:white; font-weight: bold; font-size: 16px; cursor: pointer;">
                    
                </button>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import { getCurrentInstance, reactive, ref } from 'vue';
import { useRouter } from 'vue-router';

export default {
    setup () {
        const { proxy } = getCurrentInstance();
        const router =useRouter();
        const form = ref([]);
        const state= reactive({
            id  : "",
            pw  : "",
            pw2 : "",
            phone : "",
            phone1 : "",
            nickname : "",
            birth : "",
            idcheck: '',
            nickcheck: '',
            check : false,
            phonecheck : '',
            nickcheck1 : 0,
            idcheck1 : 0,
            phonecheck1 : 0,
        })
        
        const handleIDCheck = async() => {
            if (state.id.length >= 4){
                const url = `${proxy.foo}/member/idcheck.json?id=${state.id}`;
                const headers = {"Content-Type":"application/json"};
                const {data} = await axios.get(url, {headers});
                if(data.status === 200){
                    if(data.result ===true){
                        state.idcheck ='<font color="red"></font>';
                        state.idcheck1 = 0
                    }
                    if(data.result !==true){
                        state.idcheck ='<font color="royalblue"></font>';
                        state.idcheck1 = 1
                    }                                                         
                }
            }
            else{
                state.idcheck ='<font color="red">4</font>';
            }
        };

        const handleNickcheck = async() => {
            if (state.nickname.length >= 2){
                const url = `${proxy.foo}/member/nickcheck.json?nickname=${state.nickname}`;
                const headers = {"Content-Type":"application/json"};
                const {data} = await axios.get(url, {headers});
                if(data.status === 0){
                    state.nickcheck ='<font color="red"></font>';
                    state.nickcheck1 = 0
                }
                if(data.status !== 0){
                    state.nickcheck ='<font color="royalblue"></font>';
                    state.nickcheck1 = 1
                }                                                         
            }
            else{
                state.nickcheck ='<font color="red">2</font>';
            }
        };

        const handlePhonecheck = async() => {
            if (state.phone.length >= 10){
                const url = `${proxy.foo}/member/phonecheck.json?phone=${state.phone}`;
                const headers = {"Content-Type":"application/json"};
                const {data} = await axios.get(url, {headers});
                if(data.status !== 0){
                    state.phonecheck ='<font color="royalblue"></font>';
                    state.phonecheck1 = 1;
                }
                if(data.status === 0){
                    state.phonecheck ='<font color="red"></font>';
                    state.phonecheck1 = 0;
                }
            }
            else{
                state.phonecheck ='<font color="red"></font>';
                state.phonecheck1 = 0;
            }

        };

        const submit = async() => {
            if(state.id === ""){
                alert(" ")
                form.value[0].focus();
                return false;
            }
            if(state.id.length < 4 ){
                alert(" 4 ")
                form.value[0].focus();
                return false;
            }
            if(state.id.length < 4 ){
                alert(" 4 ")
                form.value[0].focus();
                return false;
            }
            if(state.idcheck1 === 0){
                alert(" .")
                form.value[0].focus();
                return false;
            }
            if(state.pw === ''){
                alert(' .');
                form.value[1].focus();
                return false;
            }
            if(state.pw2 === ''){
                alert('  .');
                form.value[2].focus();
                return false;
            }
            if(state.pw != state.pw2){
                alert('  .');
                form.value[1].focus();
                return false;
            }
            if(state.nickname === ''){
                alert(' .');
                form.value[3].focus();
                return false;
            }
            if(state.nickname.length <2 ){
                alert(' 2 .');
                form.value[3].focus();
                return false;
            }
            if(state.nickcheck1 === 0){
                alert(' ..');
                form.value[3].focus();
                return false;
            }
            if(state.phone === ''){
                alert(' .');
                form.value[4].focus();
                return false;
            }
            if(state.phonecheck1 === 0){
                alert('  .');
                form.value[4].focus();
                return false;
            }
            if(state.birth === ''){
                alert(' .');
                form.value[5].focus();
                return false;
            }
            if(state.birth>=2023 || state.birth<=1900){
                alert('  .');
                form.value[5].focus();
                return false;
            }
            if(state.check == false ){
                alert('  .');
                form.value[6].focus();
                return false;
            }
            if(state.phone.includes('-')){
                for(let i=0; i<state.phone.split("-").length; i++){
                    state.phone1 += state.phone.split("-")[i]
                }
            }
            const url = `${proxy.foo}/member/join.json`
            const headers = {"Content-Type":"application/json"}
            const body = {
                userid : state.id,
                userpw : state.pw,  
                phone : state.phone1,
                birth : state.birth,  
                nickname :state.nickname,
                social : "normal"
            };
            const {data} = await axios.post(url,body,{headers})
            if(data.status===200){
                alert(' .');
                router.push({path:'/'});
            }
        }
        return {state,submit,handleIDCheck,form,handleNickcheck,handlePhonecheck, }
    }
}
</script>

<style lang="css" scoped>
    .div1{
        color: azure;
        padding:10px;
        border-radius: 10px;
    }
    label{
        display : inline-block;
        width   : 511px;
        text-align: left;
        font-weight: bold;
        font-size:14px;
    }
    .in{
        color: white;
        width:510px;
        height:45px;
        margin-bottom: 12px;
        margin-top: 3px;
        border: 1px solid lightgray;
        border-radius: 3px;
        padding : 0 5px;
        background-color:rgba(255, 255, 255, 0.2);
        transition: all .2s;
    }
    .in:focus{
        outline: none;
        border: 2px solid royalblue;
        transition: all .2s;
    }
    .in::placeholder{
        color:rgba(255, 255, 255, 0.65);
    }
    .sel{
        width:512px;
        height:48px;
        margin-bottom: 12px;
        margin-top: 3px;
        border: 1px solid lightgray;
        border-radius: 3px;
        padding : 0 5px;
    }
    .sel:focus{
        outline: none;
        border: 2px solid royalblue;
    }
</style>