<template>
    <div class="con">
        <v-card class="in" style="background-color:rgba(255, 255, 255, 0);box-shadow:none">
            <v-tabs v-model="state.tab" background-color="primary">
                <v-tab value="one">  </v-tab>
                <v-tab value="two">1 1 </v-tab>
            </v-tabs>

            <v-card-text>
            <v-window v-model="state.tab">
                <v-window-item value="one">
                    <v-expansion-panels variant="accordion">
                        <v-expansion-panel 
                            title="Q. OTT[i]  ?"
                            text="A. OTT  Over The Top index   OTT    ." 
                            style="background-color:rgba(255, 255, 255, 0.5); border-radius: 10px;">
                        </v-expansion-panel>
                        <v-expansion-panel
                            title="Q.    ?"
                            text="A.        ." 
                            style="background-color:rgba(255, 255, 255, 0.5)">
                        </v-expansion-panel>
                        <v-expansion-panel
                            title="Q.       ?"
                            text="A.          .      ." 
                            style="background-color:rgba(255, 255, 255, 0.5)">
                        </v-expansion-panel>
                        <v-expansion-panel
                            title="Q. 11      ?"
                            text="A.   3 .   3      ." 
                            style="background-color:rgba(255, 255, 255, 0.5)">
                        </v-expansion-panel>
                        <v-expansion-panel
                            title="Q.      ?"
                            text="A.          ,    ."
                            style="background-color:rgba(255, 255, 255, 0.5)">
                        </v-expansion-panel>
                        <v-expansion-panel
                            title="Q.    ."
                            text="A. OTT[i]      .     1 1        ."
                            style="background-color:rgba(255, 255, 255, 0.5); border-radius: 10px;">
                        </v-expansion-panel>
                    </v-expansion-panels>
                </v-window-item>
            
                <v-window-item value="two">
                    <div v-if="state.logged === true">
                        <v-table style="background-color:rgba(1, 1, 1, 0);border-radius: 10px;border-bottom:1px;">
                            <thead>
                                <tr>
                                    <th style="background-color:rgba(255, 255, 255, 0.5);border-top: none;"></th>
                                    <th style="background-color:rgba(255, 255, 255, 0.5);border-top: none;"></th>
                                    <th style="background-color:rgba(255, 255, 255, 0.5);border-top: none;"></th>
                                    <th style="background-color:rgba(255, 255, 255, 0.5);border-top: none;"></th>
                                    <th style="background-color:rgba(255, 255, 255, 0.5);border-top: none;"></th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="tmp of state.data.result" :key="tmp">
                                    <td style="text-align:center;background-color:rgba(255, 255, 255, 0.5)">{{tmp.id}}</td>
                                    <td  @click="questionone(tmp.id)" style="cursor: pointer; background-color:rgba(255, 255, 255, 0.5)">{{tmp.title}}</td>
                                    <td v-if="tmp.member.delete === 0" @click="profile(tmp.member.userid)" style="background-color:rgba(255, 255, 255, 0.5);text-align: center;font-weight:bold;cursor:pointer">{{tmp.member.nickname}}</td>
                                    <td v-if="tmp.member.delete === 1" style="background-color:rgba(255, 255, 255, 0.5);text-align: center;"> </td>
                                    <td v-if="tmp.answerYN === 1" style="color:green; text-align:center;background-color:rgba(255, 255, 255, 0.5)"></td>
                                    <td v-if="tmp.answerYN === 0" style="color:red; text-align:center;background-color:rgba(255, 255, 255, 0.5)"></td>
                                    <td style="background-color:rgba(255, 255, 255, 0.5);text-align:center;">{{tmp.regdate}}</td>
                                </tr>
                            </tbody>
                        </v-table>
                        <div class="text-center">
                            <v-pagination
                            v-model="state.page"
                            :length=(state.data.total/15)+1 @click="page(state.page)"
                            ></v-pagination>
                        </div>
                        
                        <div class="float">
                            <v-btn @click="question" rounded variant="outlined" class="btn"></v-btn>
                        </div>
                    </div>
                    <div v-else>
                        <br /><br /><br /><br />
                        <span style="color:red;font-weight:bold;font-size:20px">! </span>
                        <span>1:1      .</span>
                        <br /><br /><br /><br />
                    </div>
                </v-window-item>    
            </v-window>
            </v-card-text>
        </v-card>


    </div>
</template>

<script>
import { computed, reactive } from '@vue/reactivity'
import axios from 'axios';
import { getCurrentInstance, onMounted } from 'vue';
import { useRouter } from 'vue-router'
import { useStore } from 'vuex';
export default {
    setup () {
        const { proxy } = getCurrentInstance();
        const store = useStore();
        const router = useRouter();
        const state = reactive({
            logged : computed(()=> store.getters.getLogged),
            from : sessionStorage.getItem("from"),
            tab : "one",
            data : "",
            token : sessionStorage.getItem("token"),
            page : 1,
        })

        onMounted(()=>{
            if(state.from === "/questionone"){
                state.tab = "two";
            }
            handledata();
        })

        const handledata = async() => {
            if(state.logged === true){
                const url = `${proxy.foo}/center/QNAList.json?no=${state.page}`
                const headers = {
                    "content-type":"application/json",
                    "TOKEN":state.token
                }
                const {data} = await axios.get(url,{headers})
                state.data = data
            }
        }

        const question = () =>{
            if(state.logged === false){
                alert("  .")
            }
            else{
                router.push({path:'/question'})
            }
        }
        const questionone = (no) =>{
            router.push({path:'/questionone',query:{id:no}})
        }

        const page = (no) =>{
            state.page=no
            handledata()
        }

        const profile = (userid) => {
            router.push({path:'/userprofile', query:{id:userid}})
        }



        return {state,question,questionone,page, profile}
    }
}
</script>

<style lang="css" scoped>
    .con{
        background-color: rgba(255, 255, 255, 0.6);
        padding:30px;
        border-radius: 10px;
    }
    .tbl{
        border-radius: 8px;
        margin-right: 3px;
        padding:5px;
        width:100%;
    }
    th{
        height:40px;
        border-top: 1px solid gray;
        border-bottom: 1px solid gray;
        background-color: rgba(65, 105, 225, 0.219);
    }
    td{
        height:30px;
        padding:0 5px;
        border-bottom: 1px solid gray;
    }
    .float{
        float: right;
    }
    .btn{
        margin-right:4px;
        border: none;
        background-color: rgba(31, 57, 133, 0.555);
        color: white;
    }
</style>