<template>
    <div class="container">
        <h2 style="border-radius:10px; padding:5px; margin-bottom:10px; display:inline-block"> </h2>
        <div style="text-align:center">
            <div>
                <label style="display:inline-block;width:100px;font-weight:bold"></label>
                <input type="text" v-model="state.userid" style="width:400px;border-radius:10px;height:35px;background-color:rgba(255, 255, 255, 0.3);padding-left:10px;" placeholder=" ." /><br />
            </div>
            <div style="margin-top:5px">
                <label style="display:inline-block;width:100px;font-weight:bold"></label>
                <input type="number" v-model="state.birth" style="width:400px;border-radius:10px;height:35px;background-color:rgba(255, 255, 255, 0.3);padding-left:10px;" placeholder="ex) 1994" /><br />
            </div>
            <div style="margin-top:5px">
                <label style="display:inline-block;width:100px;font-weight:bold"></label>
                <input type="text" v-model="state.phone" style="width:400px;border-radius:10px;height:35px;background-color:rgba(255, 255, 255, 0.3);padding-left:10px;" placeholder=" ('-')  ." /><br />
            </div>
            <input type="button" @click="submit()" value=" " style=" width:400px;border-radius:10px; height:40px; margin:5px 0 0 100px;background-color:rgba(0, 0, 0, 0.3);color:white" />
        </div>
        <div>
            <v-dialog v-model="state.dialog" persistent style="text-align:center">
                <div style="background-color:rgba(255, 255, 255, 0.5);backdrop-filter: blur(10px);color:white;width:500px;padding:50px;border:1px solid gray;border-radius:10px; margin-left:auto; margin-right:auto;">
                    <h3 style="color:black"> </h3> 
                    <hr />
                    <div>
                        <label style="display:inline-block;width:130px;font-weight:bold;color:black"> </label>
                        <input type="password" v-model="state.userpw" style="width:350px;border-radius:10px;height:35px;background-color:rgba(255, 255, 255, 0.4);padding-left:10px;" />
                    </div>
                    <div>
                        <label style="display:inline-block;width:120px;font-weight:bold;color:black"> </label>
                        <input type="password" v-model="state.userpw1" style="width:350px;border-radius:10px;height:35px;background-color:rgba(255, 255, 255, 0.4);padding-left:10px;" />
                    </div>
                    <div style="margin-top:10px">
                        <button style="background-color:rgba(0, 0, 0, 0.3); border-radius:10px; padding:5px 10px;color:white;" @click="submit2()" > </button>
                        <button style="background-color:rgba(0, 0, 0, 0.3); border-radius:10px; padding:5px 10px;color:white; margin-left:10px;" @click="state.dialog=false"></button>
                    </div>
                </div>
            </v-dialog>
        </div>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import axios from 'axios'
import { useRouter } from 'vue-router'
import { getCurrentInstance } from '@vue/runtime-core'
export default {
    setup () {
        const { proxy } = getCurrentInstance();
        const router = useRouter();
        const state = reactive({
            userid : "",
            birth : "",
            phone : "",
            dialog : false,
            userpw : "",
            userpw1 : "",
            social : "normal",
        })

        const submit = async() => {
            if(state.userid === ""){
                alert(' ');
                return false;
            }
            if(state.birth === ""){
                alert(' ');
                return false;
            }
            if(state.phone === ""){
                alert(' ');
                return false;
            }
            const url = `${proxy.foo}/member/findpw.json`;
            const headers = {"Content-Type":"application/json"};
            const body = {
                userid : state.userid,
                birth : state.birth,
                phone : state.phone,
                social : state.social
            }
            const {data} = await axios.post(url, body, {headers});
            if(data.status===200){
                state.dialog=true;
            }
            else{
                alert("    .  .")
            }
        }

        const submit2 = async() => {
            if(state.userpw === ''){
                alert('  .');
                return false;
            }
            if(state.userpw != state.userpw1){
                alert('  .');
                return false;
            }
            const url = `${proxy.foo}/member/findpw2.json`;
            const headers = {"Content-Type":"application/json"};
            const body = {
                userid : state.userid,
                userpw : state.userpw,
            }
            const {data} = await axios.put(url, body, {headers});
            if(data.status===200 && data.result===1){
                alert("  .")
                router.push({path:'/login'});
            }
        }

        return {state, submit, submit2}
    }
}
</script>

<style lang="css" scoped>
    .container{
        background-color: rgba(255, 255, 255, 0.6);
        padding:30px;
        margin:0 200px;
        min-width:600px;
        border-radius: 10px;
        text-align: center;
        margin-top:150px
    }
</style>