<template>
    <div style="background-color:rgba(255, 255, 255, 0.4); margin:2px; border-radius:10px; padding:25px;">
        <h2></h2><br />
        <div>
            <label style="font-weight:bold"></label><br />
            <p style="font-size:small">   "password" .</p>
            <input type="password" class="in" v-model="state.pw"/><br />
        </div>
        <label style="font-weight:bold"></label>
        <br /><span style="font-size:large; color:red; font-weight:bold">!</span>
        <span style="font-size:small">       .</span>
        <br /><span style="font-size:small">   "</span><span style="font-size:small; font-weight:bold"></span><span style="font-size:small">"  .</span>
        <input type="text" class="in" v-model="state.deleteok" /><br />
        <v-btn @click="handleDelete" style="background-color:rgba(0, 0, 0, 0.2); margin:2px; border-radius:10px; color:white;"></v-btn>
    </div>
</template>

<script>
import axios from 'axios';
import { reactive } from '@vue/reactivity';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';
import { computed, getCurrentInstance } from '@vue/runtime-core';
export default {
    setup () {
        const { proxy } = getCurrentInstance();
        const store = useStore();
        const router =useRouter();
        const state = reactive({
            social : computed(() => store.getters.getSocial),
            pw : '',
            deleteok : '',
            token : sessionStorage.getItem("token"),
        });
        
        const handleDelete = async() =>{
            if(state.pw === "password" && state.social === "naver"){
                state.pw = "null"
            }
            if(state.pw === "password" && state.social === "kakao"){
                state.pw = "null"
            }
            if(state.pw === ""){
                alert(" .")
                return false;
            }
            if(state.deleteok === ""){
                alert("  .")
                return false;
            }
            if(state.deleteok != ""){
                alert("   .")
                return false;
            }
            if(!confirm(' ?')){
                alert(" .")
                return false;
            }
            const url = `${proxy.foo}/member/delete.json`;
            const headers = {"Content-Type" : "application/json",
                        "TOKEN" : state.token            
            };
            const body = {
                userpw: state.pw,
            };
            const {data} = await axios.delete(url, {headers:headers, data:body});
            if(data.result===1){
                sessionStorage.removeItem("token");
                sessionStorage.removeItem("role");
                sessionStorage.removeItem("ban");
                sessionStorage.removeItem("imageno");
                sessionStorage.removeItem("nickname");
                sessionStorage.removeItem("social");
                store.commit('setLogged', false);
                store.commit('setRole', null);
                store.commit('setBan', null);
                store.commit('setToken', null);
                store.commit('setImageno', null);
                store.commit('setNickname', null);
                store.commit('setSocial', null);
                alert(' .')
                router.push({path:'/'});
            }
            else{
                alert("  .")
            }
        };

        return {state,handleDelete}
    }
}
</script>

<style lang="css" scoped>
.in{
    width:100%;
    height:45px;
    margin-bottom: 12px;
    margin-top: 3px;
    border: 1px solid white;
    border-radius: 5px;
    padding : 0 5px;
    background-color: rgba(255, 255, 255, 0.25);
    transition: all .2s;
}
.in:focus{
    outline: none;
    border: 2px solid royalblue;
    transition: all .2s;
}
</style>